<template>
  <div></div>
</template>

<script>
export default {
  data() {
    return {
      redirectUrl: ''
    }
  },
  mounted() {
    this.redirectUrl = decodeURIComponent(this.$route.query.redirectUrl)
    // console.log(this.redirectUrl, 'redirectUrl')
    this.doLogin()
  },
  methods: {
    doLogin() {
      this.checkLoginFn().then(res => {
        if (res === 400) {
          this.$store.dispatch('callUplusLoginPage').then(reslogin => {
            if (reslogin !== 400) {
              this.$store.dispatch('uplusAccount/getUplusToken').then(result => {
                let sdToken = result.retData.user_center_access_token
                this.$store.dispatch('uplusAccount/getAuthorize', { sdToken }).then(rep => {
                  if (rep !== 400) {
                    this.$router.replace(this.redirectUrl.split('/sgmobile')[1])
                  }
                })
              })
            }
          })
        } else {
          // location.replace(this.redirectUrl)
          this.$store.dispatch('uplusAccount/getAuthorize', { sdToken: res.retData.user_center_access_token }).then(rep => {
            if (rep !== 400) {
              this.$router.replace(this.redirectUrl.split('/sgmobile')[1])
            }
          })
        }
      })
    },
    checkLoginFn() {
      return this.$store.dispatch('uplusAccount/getUplusToken')
    }
  }
}
</script>

